<template>
  <div id="Management">
    <div id="wrapper-bgbtm" style="width: 700px; margin: 0 auto">
      <form role="form" method="post" action="${root}/member/modifyMember">
        <div class="form-group" id="divId">
          <label for="inputId" class="col-lg-2 control-label"></label>
          <div class="col-lg-10">
            <input
              type="text"
              class="form-control onlyAlphabetAndNumber"
              id="id"
              data-rule-required="true"
              placeholder=""
              maxlength="30"
              value="${userinfo.id}"
              readonly="readonly"
              name="id"
              v-model="user.id"
            />
          </div>
        </div>
        <div class="form-group" id="divPassword">
          <label for="inputPassword" class="col-lg-2 control-label"
            ></label
          >
          <div class="col-lg-10">
            <input
              type="password"
              class="form-control"
              id="password"
              name="pw"
              data-rule-required="true"
              placeholder=""
              v-model="user.pw"
              maxlength="30"
              required="required"
            />
          </div>
        </div>
        <div class="form-group" id="divPasswordCheck">
          <label for="inputPasswordCheck" class="col-lg-2 control-label"
            > </label
          >
          <div class="col-lg-10">
            <input
              type="password"
              class="form-control"
              id="passwordCheck"
              data-rule-required="true"
              placeholder=" "
              maxlength="30"
              required="required"
              v-model="user.pwChk"
            />
          </div>
        </div>
        <div class="form-group" id="divName">
          <label for="inputName" class="col-lg-2 control-label"></label>
          <div class="col-lg-10">
            <input
              type="text"
              class="form-control onlyHangul"
              id="name"
              data-rule-required="true"
              placeholder="  ."
              maxlength="15"
              value=""
              required="required"
              name="name"
              v-model="user.name"
            />
          </div>
        </div>

        <div class="form-group" id="divNickname">
          <label for="inputNickname" class="col-lg-2 control-label"></label>
          <div class="col-lg-10">
            <input
              type="text"
              name="nickName"
              class="form-control"
              id="nickname"
              data-rule-required="true"
              placeholder=""
              maxlength="15"
              value=""
              required="required"
              v-model="user.nickname"
            />
          </div>
        </div>

        <div class="form-group" id="divEmail">
          <label for="inputEmail" class="col-lg-2 control-label"></label>
          <div class="col-lg-10">
            <input
              type="email"
              class="form-control"
              id="email"
              data-rule-required="true"
              placeholder=""
              maxlength="40"
              value=""
              required="required"
              name="email"
              v-model="user.email"
            />
          </div>
        </div>
        <div class="form-group" id="divPhoneNumber">
          <label for="inputPhoneNumber" class="col-lg-2 control-label"
            > </label
          >
          <div class="col-lg-10">
            <input
              type="tel"
              class="form-control onlyNumber"
              id="phoneNumber"
              data-rule-required="true"
              name="phoneNumber"
              placeholder="-   ."
              maxlength="11"
              value=""
              required="required"
              v-model="user.phoneNumber"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="inputPhoneNumber" class="col-lg-2 control-label"
            ></label
          >
          <div class="col-lg-10">
            <select
              class="form-control"
              id="gender"
              name="gender"
              v-model="user.gender"
            >
              <option value="M"></option>
              <option value="F"></option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <label for="inputEmailReceiveYn" class="col-lg-2 control-label"
            > </label
          >
          <div class="col-lg-10">
            <label class="radio-inline">
              <input
                type="radio"
                id="emailReceiveYn"
                name="emailCheck"
                value="Y"
                checked
                v-model="user.emailCheck"
              />
              .
            </label>
            <label class="radio-inline">
              <input
                type="radio"
                id="emailReceiveYn"
                name="emailCheck"
                value="N"
                v-model="user.emailCheck"
              />
               .
            </label>
          </div>
        </div>
        <div class="form-group">
          <label for="inputPhoneNumber" class="col-lg-2 control-label"
            >SMS </label
          >
          <div class="col-lg-10">
            <label class="radio-inline">
              <input
                type="radio"
                id="smsReceiveYn"
                v-model="user.phoneCheck"
                value="Y"
                checked
              />
              .
            </label>
            <label class="radio-inline">
              <input
                type="radio"
                id="smsReceiveYn"
                v-model="user.phoneCheck"
                value="N"
              />
               .
            </label>
          </div>
        </div>
        <div class="form-group">
          <label for="memberInfo" class="col-lg-2 control-label"
            ></label
          >
          <div class="col-lg-10" id="memberInfo">
            <div class="radio">
              <label>
                <input
                  type="radio"
                  v-model="user.memberInfoYn"
                  name="memberInfoYn"
                  value="Y"
                  checked
                />
                .
              </label>
            </div>
            <div class="radio">
              <label>
                <input
                  type="radio"
                  v-model="user.memberInfoYn"
                  name="memberInfoYn"
                  value="N"
                />
                 .
              </label>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="col-lg-offset-2 col-lg-10">
            <button
              type="button"
              id="modified"
              class="btn btn-primary"
              @click="userModify"
            >
               
            </button>
            <button type="button" @click="remove" class="btn btn-primary">
               
            </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
export default {
  data() {
    return {
      user: {
        id: localStorage.getItem('id'),
        pw: '',
        pwChk: '',
        name: '',
        nickname: '',
        email: '',
        phoneNumber: '',
        phoneCheck: '',
        gender: '',
        emailCheck: '',
        memberInfoYn: '',
      },
    };
  },
  methods: {
    remove() {
      let isOk = confirm('?');

      if (isOk) {
        axios
          .delete(`${SERVER_URL}/member/deleteMember/${this.user.id}`)
          .then((res) => {
            if (res.data.msg == 'fail') {
              alert('');
            } else if (res.data.msg == 'success') {
              alert('');
            }
          })
          .catch(() => {
            this.errored = true;
            alert('');
          })
          .finally(() => {
            this.$store
              .dispatch('LOGOUT')
              .then(() => this.$router.replace(`/`).catch(() => {}));
          });
      }
    },
    userModify() {
      if (
        this.user.pw == '' ||
        this.user.pwChk == '' ||
        this.user.nickname == '' ||
        this.user.email == '' ||
        this.user.phoneNumber == '' ||
        this.user.name == '' ||
        this.user.gender == '' ||
        this.user.emailCheck == '' ||
        this.user.phoneCheck == '' ||
        this.user.memberInfoYn == ''
      ) {
        alert('');
      } else if (this.user.pw == this.user.pwChk) {
        axios
          .put(`${SERVER_URL}/member/modifyMember`, {
            id: this.user.id,
            pw: this.user.pw,
            pwChk: this.user.pwChk,
            name: this.user.name,
            nickName: this.user.nickname,
            email: this.user.email,
            phoneNumber: this.user.phoneNumber,
            gender: this.user.gender,
            emailCheck: this.user.emailCheck,
            phoneCheck: this.user.phoneCheck,
            memberInfoYn: this.user.memberInfoYn,
          })
          .then((res) => {
            if (res.data.msg == 'fail') {
              alert('');
              this.$router.go(this.$router.current);
            } else if (res.data.msg == 'success') {
              alert('');
              this.$router.push('/');
            }
          })
          .catch(() => {
            this.errored = true;
            alert('');
            this.$router.go(this.$router.current);
          })
          .finally(() => {
            this.loading = false;
          });
      } else {
        alert(' ');
      }
    },
  },
};
</script>
<style>
#Management {
  background-color: #ffffff;
  overflow: hidden;
}
</style>
