<template>
  <div id="wrapper" style="height: 700px">
    <div class="container" align="center">
      <div class="col-lg-6" align="center">
        <br />
        <h2>{{ this.isTitle }} </h2>
        <form id="writeform" method="post" action="">
          <div class="form-group" align="left">
            <label for="subject">:</label>
            <input
              type="text"
              class="form-control"
              id="subject"
              name="subject"
              v-model="subject"
            />
          </div>
          <div class="form-group" align="left">
            <label for="content">:</label>
            <textarea
              class="form-control"
              rows="15"
              id="content"
              name="content"
              v-model="content"
            ></textarea>
          </div>
          <button type="button" class="btn btn-primary" @click="writeArticle">
            
          </button>
          <button type="reset" class="btn btn-warning"></button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import { mapGetters } from 'vuex';
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
export default {
  props: ['title'],
  data() {
    return {
      isTitle: '',
      subject: ' ',
      content: ' ',
      userid: '',
    };
  },
  methods: {
    writeArticle() {
      if (this.subject == ' ' || this.content == ' ') {
        alert(' !');
        return;
      }

      if (this.title == 'notice') {
        axios
          .post(`${SERVER_URL}/board/noticeWrite`, {
            userid: localStorage.getItem('id'),
            subject: this.subject,
            content: this.content,
          })
          .then(() => {
            alert('Q&A   ');
            this.$router.push('/notice');
          });
      }
      if (this.title == 'guestbook') {
        axios
          .post(`${SERVER_URL}/guestbook/noticeWrite`, {
            subject: this.subject,
            content: this.content,
          })
          .then(() => {
            alert('  ');
            this.$router.push('/guestbook');
          });
      }
    },
  },
  computed: {
    ...mapGetters(['getAccessToken', 'getUserId', 'getUserName']),
  },
  mounted() {
    if (this.title == 'guestbook') {
      this.isTitle = '';
    } else {
      this.isTitle = 'Q&A';
    }
  },
};
</script>
