<template>
  <div>
    <b-modal id="modal-lg" size="lg" ref="my-modal" hide-footer title="">
      <template>
        <div id="map" class="map" ref="map"></div>
      </template>
      <b-button class="mt-3" variant="outline-danger" block @click="hideModal"
        >Close</b-button
      >
    </b-modal>
  </div>
</template>

<script>
const KAKAO_API_KEY = process.env.VUE_APP_KAKAO_API_KEY;
export default {
  props: ['road'],
  watch: {
    road: {
      deep: true,
      handler() {
        this.kakao && this.kakao.maps
          ? this.initMap()
          : this.addKakaoMapScript();
        this.$refs['my-modal'].show();
      },
    },
  },
  mounted() {},
  methods: {
    hideModal() {
      this.$refs['my-modal'].hide();
    },
    addKakaoMapScript() {
      const script = document.createElement('script');
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src = `http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${KAKAO_API_KEY}`;
      document.head.appendChild(script);
    },
    initMap() {
      // var roadviewContainer = document.getElementById('map'); //   DOM 
      var roadviewContainer = this.$refs['map'];
      var roadview = new kakao.maps.Roadview(roadviewContainer); // 
      var roadviewClient = new kakao.maps.RoadviewClient(); //  ID   helper

      //     
      var position = new kakao.maps.LatLng(this.road.lat, this.road.lng); // .

      roadviewClient.getNearestPanoId(position, 50, function(panoId) {
        roadview.setPanoId(panoId, position); //panoId    
      }); //    
    },
  },
};
</script>

<style>
.map {
  width: 100%;
  height: 500px;
}
#loadview {
  height: 600px;
  background-color: #ffffff;
}
</style>
