<template>
  <div id="main">
    <div id="wrapper-bgbtm">
      <div id="featured" class="container">
        <div id="fbox1">
          <b-link to="/priceview"
            ><img src="@/assets/pic01.jpg" alt=""
          /></b-link>
          <h2> </h2>
          <p>
                   !   .
          </p>
          <b-link to="/priceview" class="button"><span>More Info</span></b-link>
        </div>
        <div id="fbox2">
          <b-link to="/starview"><img src="@/assets/pic02.jpg" alt=""/></b-link>

          <h2> </h2>
          <p>
                .  !
                .
          </p>
          <b-link to="/starview" class="button"><span>More Info</span></b-link>
        </div>
        <div id="fbox3">
          <b-link to="/storeview"
            ><img src="@/assets/pic03.jpg" alt=""
          /></b-link>
          <h2>  </h2>
          <p>
                .  !
                .
          </p>
          <b-link to="/storeview" class="button"><span>More Info</span></b-link>
        </div>
      </div>
      <div id="page" class="container">
        <div id="content">
          <h4>Happy house Vue</h4>
          <span class="byline"><strong> </strong></span>
          <img src="@/assets/pic04.jpg" width="300" height="200" alt="" />
          <p>  </p>
          <b-link to="/" class="button"><span>More Info</span></b-link>
        </div>
        <div id="sidebar">
          <div>
            <h2>  </h2>
            <ul v-for="(deal, index) in dealRank" :key="index" class="style1">
              <li>
                <strong
                  ><p class="noticeContent">
                    {{ deal.aptName }}
                  </p></strong
                >
                <span class="writer">: {{ deal.viewCnt }}</span>
                <br />
                <br />
              </li>
            </ul>
          </div>
        </div>
        <div id="sidebar">
          <div>
            <h2></h2>
            <ul
              v-for="(mainnotice, index) in mainNotice"
              :key="index"
              class="style1"
            >
              <li>
                <strong
                  ><p class="noticeContent">
                    {{ mainnotice.subject }}
                  </p></strong
                >
                <span class="writer">: {{ mainnotice.userid }}</span>
                <br />
                <span class="date"> : {{ mainnotice.regtime }} + </span>
              </li>
            </ul>

            <b-link to="/guestbook" class="button2">More Info</b-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
export default {
  data() {
    return {
      key: '',
      word: '',
      mainNotice: [],
      dealRank: [],
    };
  },
  created() {
    axios
      .get(`${SERVER_URL}/guestbook/subMain`)
      .then((response) => {
        this.mainNotice = response.data;
      })
      .catch(() => (this.errored = true))
      .finally(() => (this.loading = false));

    axios
      .get(`${SERVER_URL}/map/dealCount`)
      .then((response) => {
        this.dealRank = response.data;
      })
      .catch(() => (this.errored = true))
      .finally(() => (this.loading = false));
  },
};
</script>
<style>
#main {
  overflow: hidden;
  background-color: #ffffff;
}
#sidebar {
  float: right;
  width: 350px;
}

#sidebar h2 {
  font-size: 2em;
  margin-bottom: 1em;
  font-weight: 600;
}
</style>
